import React, { useState, useEffect } from 'react';
import { Controller, Scene } from 'react-scrollmagic';
import { Tween, Timeline } from 'react-gsap';
import { Link } from 'gatsby';
import * as styles from './styles.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import MobileNav from './MobileNav';

library.add(fab);

export const ReactNavbar = ({ color, logo, menu, social }) => {
  const navLinks = menu;
  const socialIcon = social;
  const background = color;
  const logoUrl = logo;

  const [hasRan, setHasRan] = useState(false);
  const [screenSize, setScreenSize] = useState({
    height: 0,
    width: 0,
  });
  const updateScreenSize = () => {
    setScreenSize({ width: window.innerWidth, height: window.innerHeight });
  };
  useEffect(() => {
    if (!hasRan) {
      setHasRan(true);
      updateScreenSize();
    }
    window.addEventListener('resize', updateScreenSize);
    return () => {
      window.removeEventListener('resize', updateScreenSize);
    };
  }, [hasRan, screenSize]);

  return (
    <div>
      {hasRan ? (
        <>
          {screenSize.width < 1150 ? (
            <MobileNav
              width={screenSize.width}
              logoUrl={logoUrl}
              background={background}
              navLinks={navLinks}
              socialIcon={socialIcon}
            />
          ) : (
            <Controller>
              <Scene triggerHook="onLeave" duration={300} triggerElement={null}>
                {(progress) => (
                  <Timeline totalProgress={progress} paused>
                    <Tween
                      from={{ height: '80px' }}
                      to={{ height: '60px', background: background }}
                    >
                      <div className={styles.header}>
                        <div className={styles.navLogo}>
                          <Link to="/">
                            <div className="logo-container">
                              <Timeline totalProgress={progress} paused>
                                <Tween
                                  from={{ height: '70px' }}
                                  to={{ height: '50px' }}
                                >
                                  <img
                                    className={styles.LogoImg}
                                    src={logoUrl}
                                    alt="logo"
                                  />
                                </Tween>
                              </Timeline>
                            </div>
                          </Link>
                        </div>

                        <div className={styles.navLinks}>
                          <ul>
                            {navLinks.map((link, i) => (
                              <li key={i}>
                                <Link
                                  to={link.to}
                                  activeClassName={styles.home}
                                >
                                  {link.name}
                                </Link>
                              </li>
                            ))}
                          </ul>
                        </div>
                        <div className={styles.navSocial}>
                          <ul>
                            {socialIcon.map((icon, i) => (
                              <li key={i}>
                                <a
                                  target="_blank"
                                  rel="noreferrer"
                                  href={icon.url}
                                  aria-label={icon.name}
                                >
                                  <FontAwesomeIcon icon={icon.icon} />
                                </a>
                              </li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    </Tween>
                  </Timeline>
                )}
              </Scene>
            </Controller>
          )}
        </>
      ) : (
        <></>
      )}
    </div>
  );
};
