import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import CookieConsent from 'react-cookie-consent';

import '../assets/sass/main.scss';
import Footer from './Footer';
import NavBar from './Navbar';
import Seo from './Seo';

class Layout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isPreloaded: true,
    };
    this.customSEO = this.props.customSEO;
  }

  componentDidMount() {
    this.timeoutId = setTimeout(() => {
      this.setState({ isPreloaded: false });
    }, 100);
  }

  componentWillUnmount() {
    if (this.timeoutId) {
      clearTimeout(this.timeoutId);
    }
  }

  render() {
    const { children } = this.props;
    const { isPreloaded } = this.state;
    return (
      <>
        <Seo
          location={this.props.location}
          title={this.props.title}
          image={this.props.image}
          description={this.props.description}
          article={this.props.article}
        />
        <div
          className={
            isPreloaded ? 'landing main-body is-preload' : 'landing main-body'
          }
        >
          <div id="page-wrapper">
            <NavBar />
            {children}
            <Footer />

            <CookieConsent
              location="bottom"
              buttonText="Akzeptieren"
              cookieName="gatsby-gdpr-google-analytics"
              containerClasses="cookie-banner"
              buttonClasses="button cookie-banner-button"
              disableButtonStyles={true}
              expires={150}
            >
              Wenn du unsere Seite weiterhin nutzt, stimmst du unserem Einsatz
              von Cookies, wie es in der{' '}
              <Link
                to="/datenschutz/"
                style={{
                  textDecoration: 'underline',
                  cursor: 'pointer',
                  marginLeft: '2px',
                }}
              >
                Datenschutzerklärung
              </Link>{' '}
              aufgeführt ist, zu.
            </CookieConsent>
          </div>
        </div>
      </>
    );
  }
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string,
  description: PropTypes.string,
  image: PropTypes.string,
  article: PropTypes.bool,
};
Layout.defaultProps = {
  title: null,
  description: null,
  image: null,
  article: false,
};

export default Layout;
